import React, {Component} from 'react';
import { BrowserRouter, Route, Link } from 'react-router-dom'; 
// Pages:
import Hello from './Hello.js'
import Projects from './Projects.js'; 
import Articles from './Articles.js'; 
import About from './About.js';
// Style:
import './static/css/Application.css';

class Application extends Component {
  
    
    render(){
      // function chooseItem() {
      //   var x = document.getElementById("item");
      //   if (x.style.display === "block") {
      //     x.style.display = "none";
      //   } else {
      //     x.style.display = "block";
      //   }
      // }
        return (
            <BrowserRouter>
            <div className="Application">
                <div className="navigation">
                    <div className="navigation-sub">
                    <li><Link to="/" className="item">Home </Link></li>
                        <li><Link to="/projects" className="item">Projects </Link></li>
                        <li><Link to="/articles" className="item">Articles </Link></li>
                        <li><Link to="/about" className="item">About </Link></li>
                        {/* <li><Link className="item">{Logo} </Link></li> */}
                    </div>
                </div>
                <br></br>
                <br></br>
                <br></br>
            </div>
            <Route exact path="/" component={Hello}/>
            <Route exact path="/projects" component={Projects}/>
            <Route path="/articles" component={Articles}/>
            <Route path="/about" component={About}/>
            </BrowserRouter>
    );
  }
}

export default Application;